<template>
  <div class="schedule-input-view">
    <h2>Êó•Á®ãÂÆâÊéíËæìÂÖ•</h2>
    <textarea 
      v-model="scheduleInput" 
      placeholder="ËæìÂÖ•ÊÇ®ÁöÑÊó•Á®ãÂÆâÊéí..." 
      rows="4"
    ></textarea>
    <button 
      @click="submitSchedule" 
      :disabled="isSubmitting"
    >
      {{ isSubmitting ? 'Êèê‰∫§‰∏≠...' : 'Êèê‰∫§Êó•Á®ã' }}
    </button>
    <div v-if="response" class="response-area">
      <h3>ÂìçÂ∫îÔºö</h3>
      <pre>{{ response }}</pre>
    </div>
  </div>
