<script>
/* eslint-disable no-console */
import axios from 'axios';

export default {
  name: 'ScheduleInputView',
  data() {
    return {
      scheduleInput: '',
      apiKey: process.env.VUE_APP_DIFY_API_KEY,
      apiUrl: process.env.VUE_APP_DIFY_API_URL,
      userId: 'your-user-id', // ËÄÉËôë‰πüÂ∞ÜÊ≠§ÁßªËá≥ÁéØÂ¢ÉÂèòÈáè
      response: null,
      isSubmitting: false
    };
  },
  methods: {
    async submitSchedule() {
      if (this.isSubmitting) return;
      this.isSubmitting = true;

      try {
        console.log('Ê≠£Âú®ÂèëÈÄÅËØ∑Ê±Ç...');
        console.log('API URL:', this.apiUrl);
        console.log('API Key (Ââç5‰∏™Â≠óÁ¨¶):', this.apiKey.substring(0, 5));

        const requestBody = {
          inputs: {
            input: this.scheduleInput
          },
          response_mode: "blocking",
          user: this.userId
        };

        console.log('ËØ∑Ê±Ç‰Ωì:', JSON.stringify(requestBody));

        const response = await axios({
          method: 'post',
          url: this.apiUrl,
          data: requestBody,
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${this.apiKey.trim()}`
          }
        });

        console.log('ÂìçÂ∫î:', response.data);

        if (response.data && response.data.data && response.data.data.outputs && response.data.data.outputs.text) {
          this.response = response.data.data.outputs.text;
        } else {
          this.response = 'Êú™ÊâæÂà∞ÊúâÊïàÁöÑËæìÂá∫';
          console.log('API ÂìçÂ∫î‰∏≠Ê≤°ÊúâÊâæÂà∞È¢ÑÊúüÁöÑËæìÂá∫ÁªìÊûÑ');
        }

      } catch (error) {
        console.error('Êèê‰∫§Êó•Á®ãÊó∂Âá∫Èîô:', error.message);
        if (error.response) {
          console.error('ÈîôËØØÊï∞ÊçÆ:', error.response.data);
          console.error('ÈîôËØØÁä∂ÊÄÅ:', error.response.status);
          console.error('ÈîôËØØÂ§¥ÈÉ®:', error.response.headers);
          this.response = 'ÈîôËØØ: ' + JSON.stringify(error.response.data, null, 2);
        } else if (error.request) {
          console.error('Êú™Êî∂Âà∞ÂìçÂ∫î:', error.request);
          this.response = 'ÈîôËØØ: Êú™Êî∂Âà∞ÊúçÂä°Âô®ÂìçÂ∫î';
        } else {
          this.response = 'ÈîôËØØ: ' + error.message;
        }
      } finally {
        this.isSubmitting = false;
      }
    }
  }
};
</script>

