<template>
  <div class="home-view">
    <div class="header">
      <h1>ÂÆ∂Â∫≠Êó•Á®ã</h1>
      <CurrentDateTime />
    </div>
    <div class="layout">
      <div class="sidebar">
        <MonthlyCalendar 
          :events="events" 
          @date-selected="handleDateSelected"
          @search="handleSearch"
        />
        <FamilyTabs />
      </div>
      <div class="main-content">
        <ScheduleView :events="filteredEvents" />
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue';
import axios from 'axios';
import FamilyTabs from '@/components/FamilyTabs.vue';
import ScheduleView from '@/components/ScheduleView.vue';
import CurrentDateTime from '@/components/CurrentDateTime.vue';
import MonthlyCalendar from '@/components/MonthlyCalendar.vue';

export default {
  name: 'HomeView',
  components: {
    FamilyTabs,
    ScheduleView,
    CurrentDateTime,
    MonthlyCalendar
  },
  setup() {
    const events = ref([]);
    const selectedDate = ref(null);
    const searchQuery = ref('');
    let ws;
    let isPolling = false;

    const filteredEvents = computed(() => {
      return events.value.filter(event => {
        const matchesDate = !selectedDate.value || new Date(event.date).toDateString() === selectedDate.value.toDateString();
        const matchesSearch = !searchQuery.value || event.title.toLowerCase().includes(searchQuery.value.toLowerCase());
        return matchesDate && matchesSearch;
      });
    });

    function handleDateSelected(date) {
      selectedDate.value = date;
    }

    function handleSearch(query) {
      searchQuery.value = query;
    }

    const connectWebSocket = () => {
      const wsUrl = process.env.VUE_APP_WS_URL || 'ws://localhost:3000';
      console.log('Â∞ùËØïËøûÊé•Âà∞ WebSocket:', wsUrl);
      ws = new WebSocket(wsUrl);

      ws.onopen = () => {
        console.log('WebSocket ËøûÊé•Â∑≤Âª∫Á´ã');
        isPolling = false; // ÈáçÁΩÆËΩÆËØ¢Ê†áÂøó
      };

      ws.onmessage = (event) => {
        console.log('Êî∂Âà∞ WebSocket Ê∂àÊÅØ:', event.data);
        try {
          const data = JSON.parse(event.data);
          if (data.type === 'initial') {
            events.value = data.events;
          } else if (data.type === 'newEvent') {
            events.value.push(data.event);
          }
        } catch (error) {
          console.error('Â§ÑÁêÜ WebSocket Ê∂àÊÅØÊó∂Âá∫Èîô:', error);
        }
      };

      ws.onerror = (error) => {
        console.error('WebSocket ÈîôËØØ:', error);
        if (!isPolling) {
          console.log('ÂàáÊç¢Âà∞ËΩÆËØ¢Ê®°Âºè');
          pollForEvents();
        }
      };

      ws.onclose = (event) => {
        console.log(`WebSocket ËøûÊé•ÂÖ≥Èó≠„ÄÇ‰ª£Á†Å: ${event.code}, ÂéüÂõ†: ${event.reason}`);
        if (!isPolling) {
          console.log('5ÁßíÂêéÂ∞ùËØïÈáçÊñ∞ËøûÊé•...');
          setTimeout(connectWebSocket, 5000);
        }
      };
    };

    const pollForEvents = async () => {
      if (isPolling) return;
      isPolling = true;

      const poll = async () => {
        try {
          const response = await axios.get('/api/events');
          events.value = response.data;
          console.log('ÈÄöËøáËΩÆËØ¢Ëé∑ÂèñÂà∞‰∫ã‰ª∂:', events.value);
        } catch (error) {
          console.error('ËΩÆËØ¢ÈîôËØØ:', error);
        }

        if (isPolling) {
          setTimeout(poll, 5000); // ÊØè5ÁßíËΩÆËØ¢‰∏ÄÊ¨°
        }
      };

      poll();
    };

    onMounted(() => {
      connectWebSocket();
    });

    onUnmounted(() => {
      if (ws) {
        ws.close();
      }
      isPolling = false; // Á°Æ‰øùÂú®ÁªÑ‰ª∂Âç∏ËΩΩÊó∂ÂÅúÊ≠¢ËΩÆËØ¢
    });

    return {
      events,
      filteredEvents,
      handleDateSelected,
      handleSearch,
    };
  }
};
</script>

<style src="./HomeView.css" scoped></style>